.tab-pp .header {
  display: flex;
  padding: 0px 8px;
  box-sizing: border-box;
  justify-content: flex-start;
  align-items: center;
  overflow-x: auto;
}
.tab-pp .header .itemlabel {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.5rem 0.65rem;
  min-height: 48px;
  box-sizing: border-box;
  white-space: nowrap;
  text-transform: capitalize;
  cursor: pointer;
  opacity: 0.7;
  font-size: inherit;
  border: unset;
}
.tab-pp .header .itemlabel[disabled] {
  opacity: 0.4;
  cursor: not-allowed;
}
.tab-pp .header .itemlabel.simple {
  position: relative;
  opacity: 1;
}
.tab-pp .header .itemlabel.bottomLine {
  position: relative;
  opacity: 1;
}
.tab-pp .header .itemlabel.bottomLine div {
  position: absolute;
  width: 100%;
  bottom: 0;
  height: 3px;
}
.tab-pp .header .itemlabel.button {
  position: relative;
  opacity: 1;
  color: inherit;
  border-radius: 4px;
}
.tab-pp .body {
  position: relative;
  height: auto;
  width: 100%;
  overflow: hidden;
}
.tab-pp .body .body-content {
  position: absolute;
  top: 0;
  left: 0;
  visibility: hidden;
  width: 100%;
  height: auto;
  transform: none;
}