.EditImage .flex-row-start {
  flex-direction: row;
  box-sizing: border-box;
  display: flex;
  place-content: flex-start;
  align-items: flex-start;
}
.EditImage .popup {
  width: 100vw;
  max-height: 100%;
  height: 100%;
  overflow: auto;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 1000;
  padding: 24px 16px;
  color: white;
  box-sizing: border-box;
  animation-name: show;
  animation-duration: 0.4s;
}
.EditImage .popup .image-container {
  margin-top: 50px;
  margin-bottom: 50px;
  width: 100%;
  flex-flow: row wrap;
  box-sizing: border-box;
  display: flex;
  place-content: flex-start center;
  align-items: flex-start;
}
.EditImage .popup .image-container .image-holder-full {
  height: auto;
  width: auto;
  position: relative;
  display: contents;
}
.EditImage .popup .material-icons {
  color: white;
}
.EditImage .popup img {
  vertical-align: middle;
  max-height: 650px;
  max-width: 100%;
  object-fit: cover;
  object-position: center;
}
@media (max-width: 1024px) {
  .EditImage .popup {
    background-color: rgba(0, 0, 0, 0.85);
    padding: 8px;
  }
  .EditImage .popup img {
    max-height: 100%;
    max-width: 100%;
  }
}
@media (max-width: 599px) {
  .EditImage .popup img {
    margin: 0px;
  }
}
.EditImage .popup .control-panel {
  color: #fff;
  background-color: rgba(0, 0, 0, 0.92);
  margin: 0px 16px;
  padding: 16px;
  max-width: 100%;
  width: 18rem;
  border-radius: 8px;
  flex-direction: column;
  box-sizing: border-box;
  display: flex;
  place-content: stretch flex-start;
  align-items: stretch;
  max-width: 100%;
  position: relative;
}
@media (max-width: 599px) {
  .EditImage .popup .control-panel {
    margin: 8px 0px;
    width: 100%;
  }
}
.EditImage .popup .control-panel .title-panel {
  padding: 0px 4px;
  font-size: 17px;
  font-weight: 500;
  margin-bottom: 16px !important;
}
.EditImage .popup .control-panel .item-panel {
  padding: 0px 0px;
  font-size: 14px;
  font-weight: 500;
}
.EditImage .image-croper {
  position: absolute;
  width: 180px;
  height: 180px;
  min-height: 64px;
  min-width: 64px;
  border: 2.5px solid #fafafa;
  box-sizing: border-box;
  resize: both;
  overflow: auto;
  opacity: 0;
  border-radius: 4px;
  overflow: hidden;
  z-index: 10;
}
.EditImage .image-croper #image-croper-header {
  padding: 0px;
  cursor: move;
  z-index: 9;
  background-color: transparent;
  color: #fff;
  height: 100%;
  display: grid;
  gap: 0px;
  grid-template-columns: repeat(3, 1fr);
  width: 100%;
  box-sizing: border-box;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  opacity: 0.8;
}
.EditImage .image-croper #image-croper-header .material-icons {
  color: #fff;
}
.EditImage .input-range {
  margin-bottom: 16px;
  margin-top: 4px;
}
.EditImage input[type=checkbox] {
  cursor: pointer;
}
.EditImage .form-field {
  margin-bottom: 16px;
}
.EditImage .form-field label {
  font-size: 12px;
  opacity: 0.92;
  margin-bottom: 4px;
  display: block;
}
.EditImage .form-field input {
  height: 34px;
  width: 100%;
  box-sizing: border-box;
  border-radius: 4px;
  border: none;
  padding: 2px 8px;
  outline: none;
}
.EditImage .form-field select {
  height: 34px;
  width: 100%;
  box-sizing: border-box;
  border-radius: 4px;
  border: none;
  padding: 2px 8px;
  outline: none;
  background-color: white;
}
.EditImage .save-btn {
  padding: 0px 8px;
  height: 34px;
  box-sizing: border-box;
  background-color: #fff;
  color: black;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.EditImage .save-btn:active {
  transform: scale(0.95);
}
.EditImage .caption {
  font-size: 11px;
  margin-top: 2px !important;
  margin-bottom: 0px !important;
}
.EditImage button[disabled] {
  filter: brightness(0.5);
  cursor: not-allowed;
}
.EditImage button:active {
  transform: scale(0.95);
}
.EditImage.dark .form-field select {
  background-color: unset;
}